<template>
    <div class="border">
        <q-card
        >
          <div class="text-center">
            <q-img
              :src="BASE_URL + event.image"
              :ratio="3/4"
            >
            <span class="event-text">{{ event.text }}</span><br>
            <span class="event-text">{{ display(event.createdAt) }}</span>
            </q-img>
          </div>
          </q-card>
    </div>
</template>

<script>
import APP_CONFIG from '@/config';

export default {
    props: {
        event: {
            type: Object
        }
    },
    data() {
        return {
            BASE_URL: APP_CONFIG.API_URL
        };
    },
    // created() {
    //     console.log(this.BASE_URL + this.event.image);
    // },
    methods: {
        display(date) {
            if (this.$moment(date).isSame(this.$moment(), 'day')) {
                return this.$moment(date).format('HH:mm:ss');
            }
            if (this.$moment(date).isSame(this.$moment(), 'year')) {
                return this.$moment(date).format('DD/MM - HH:mm:ss');
            }
            return this.$moment(date).format('DD/MM/YY');
        }
    }
};
</script>
