<template>
    
  <q-page
    class="window-height window-width row justify-center items-center bg-login"
  >
          <div class="column q-pa-lg">
      <div class="row">
        <q-card circle class="shadow-24" style="width:500px;height:400px; opacity: 0.8; background-color: cyan;">
          <q-card-section class="bg-grey-8">
            <h4 class="text-h5 text-blue q-my-md text-center text-bold">WELCOME APPLICATION</h4>
          </q-card-section>
          <q-card-section>
            <q-form class="q-px-sm q-pt-xl">
              <q-input @keyup.enter="login" rounded standout clearable v-model="form.username" type="email" label="Username/Email">
                <template v-slot:prepend>
                  <q-icon name="email" />
                </template>
              </q-input>
              <br>
              <q-input @keyup.enter="login" rounded standout clearable v-model="form.password" type="password" label="Password">
                <template v-slot:prepend>
                  <q-icon name="lock" />
                </template>
              </q-input>
            </q-form>
          </q-card-section>
         
          <!--<q-card-section>
            <div class="text-center q-pa-md q-gutter-md">
              <q-btn round color="indigo-7">
                <q-icon name="fab fa-facebook-f" size="1.2rem" />
              </q-btn>
              <q-btn round color="red-8">
                <q-icon name="fab fa-google-plus-g" size="1.2rem" />
              </q-btn>
              <q-btn round color="light-blue-5">
                <q-icon name="fab fa-twitter" size="1.2rem" />
              </q-btn>
            </div>
          </q-card-section> -->
          <br>
          <q-card-actions class="q-px">
            <q-btn unelevated rounded size="lg" @keyup.enter="login" @click="login()" color="amber" class="full-width text-white" label="Sign In" />
          </q-card-actions>
          <!--<q-card-section class="text-center q-pa-sm">
            <p class="text-grey-6">Forgot your password?</p>
          </q-card-section> -->
        </q-card>
      </div>
    </div>
    
  </q-page>
</template>

<script>
import axios from 'axios';
import APP_CONFIG from '@/config';

export default {
  name: 'Login',
  data () {
    return {
      form: {
        username: '',
        password: '',
        currentUserToken: '',
        errors: {}
      }
    }
  },
  created() {
    this.loadCurrentUser();
  },
  methods: {
    loadCurrentUser() {
      if (localStorage.getItem('currentUserToken')) {
        this.currentUserToken = localStorage.getItem('currentUserToken');
        console.log(this.currentUserToken);
        return this.currentUserToken;
      }
    },
    login() {
      const headers = {
        'Content-Type': 'application/json'
      }
      if (this.form.username!== '' && this.form.password!== '') {
        // console.log(this.form);
        axios.post(APP_CONFIG.ROOT_URL + 'AIViewService/api/auth/signin', this.form, headers).then((response) => {
          if (response.status === 200) {
            console.log(response);
            // console.log(response.data.data.token);
            localStorage.setItem('currentUserToken', response.data.data.token);
            this.$toasted.success('Đăng nhập thành công!').goAway(500);
            setTimeout(function() {
                this.$router.push('/');
            }.bind(this), 800);
          }
        }).catch( error => {
          this.errors = error.response;
          if (this.errors.status === 401) {
            this.$toasted.info(this.errors.data.message).goAway(500);
          } else {
            this.$toasted.error('Đăng nhập thất bại').goAway(500);
          }
          // console.log(this.errors);
        });
      } else {
        this.$toasted.error('Cần điền đủ thông tin...').goAway(500);
      }
    }
  }
}
</script>

