<template>
    <q-img :src="url"/>
</template>

<script>
import APP_CONFIG from '@/config';
export default {
    props: ['liveUrl'],
    name: 'cam-live-img',
    mounted: function() {
        this.$nextTick(function() {
            this.reloadCamInterval = setInterval(() => {
                this.url = this.liveUrl;
            }, APP_CONFIG.CAM_LIVE_RELOAD_URL);
        });
    },
    data() {
        return {
            url: this.liveUrl,
            reloadCamInterval: null,
        };
    },
    beforeDestroy() {
        if (this.reloadCamInterval) {
            clearInterval(this.reloadCamInterval);
        }
    }
};
</script>