
<template>
  <q-layout view="hHh lpR fFf">

    <q-header elevated class="bg-dark text-blue">
      <q-toolbar>
        <q-toolbar-title>
          <q-avatar>
            <img src="@/assets/logo.png">
          </q-avatar>
          EMS WELCOME SCREEN
        </q-toolbar-title>
        <i class="fa fa-sign-out-alt" style="margin-right: 40px;" @click="Logout()">Đăng xuất</i>
        <q-btn dense flat round icon="dashboard" @click="right = !right" />
      </q-toolbar>
    </q-header>

    <q-drawer v-model="right" :width="300" side="right" overlay elevated content-class="q-pa-sm bg-grey-9 text-white text-center noscroll">
      
       <q-card
          class="my-card"
          style="background: radial-gradient(circle, #35a2ff 0%, #014a88 100%)"
        >
      <q-card-section>
        <clock style="font-size: 18px; color: white; font-weight: bold;"></clock>
      <p style="font-size: 18px; color: lime; font-weight: bold;">
        ĐÃ ĐẾN: 5/10
      </p>
      <p style="font-size: 18px; color: red; font-weight: bold;">
        VẮNG: 5/10
      </p>
      </q-card-section>

      </q-card>
    
      <hr>
      <div style="height: 62%;">
        <q-table
          :data="data"
          :columns="columns"
          row-key="name"
           :rows-per-page-options="[10]"
        />
      </div>
      <br>
      <hr>

      <q-card
          class="my-card bg-secondary"
          style="margin-bottom: 5px;"
        >
      <q-card-section style="padding: 5px;">
        <q-select color="red" outlined v-model="viewSrc" option-value="id" :options="this.genListOptions(this.camerasDisplay)" label="Chọn View" style="margin-bottom: 5px;">
        <template v-slot:append>
          <q-icon name="image" />
        </template>
      </q-select>
      <q-select color="red" outlined v-model="sensorsSelected" option-value="id" :options="this.genListOptions(this.camerasDisplay)" multiple label="Chọn Sensors">
        <template v-slot:append>
          <q-icon name="camera" />
        </template>
      </q-select>
      </q-card-section>

      </q-card>

      
    </q-drawer>

    <q-page-container>
      <q-page style="background-color: grey;">
        <cam-live-img
            :liveUrl="genLiveUrl(viewSrc)"
        ></cam-live-img>
        <span
            style="position: fixed; top: 80px; left: 35%; max-width: 1060px; border-radius: 10px;"
            class="event-text-color"
        >
            <div
                class="text-center"
                style="font-weight: 600; max-width: 1000px; font-size: 2.5rem;"
                v-if="this.messageShow !== ''"
            >
                <span
                    style="padding-left: 10px"
                    >Chào mừng: </span
                >
                <span style="padding: 0 10px" v-for="itemTest in this.filterText(this.messageShow)" v-bind:key="itemTest">
                    {{ itemTest }} <br>
                </span>
            </div>
        </span>

        <span class="cols-2" v-if="eventShow !== null" style="position: fixed; width: 180px; height: 240px; left: 10px; top: 60px;">
            <NewestFaceItem :event="eventShow" />
        </span>
        <span
            style="position: fixed; bottom: 5px; left: 80px; width: 100%"
        >
            <attendance-newest-face-list
                :events="listItemDisplay"
            ></attendance-newest-face-list>
        </span>
      </q-page>
    </q-page-container>

  </q-layout>
</template>


<script>
import Clock from '@/components/Clock';
import CamLiveImg from '@/components/CamLiveImg';
import NewestFaceItem from '@/components/NewestFaceItem';
import AttendanceNewestFaceList from '@/components/NewestFaceList';
import APP_CONFIG from '@/config';
import axios from 'axios';
export default {
  components: { Clock, CamLiveImg, NewestFaceItem, AttendanceNewestFaceList },
  mounted: function() {
      this.$nextTick(function() {
          this.getEventNewestAsLandMark();
          this.getMonitorList();
          this.getCameraList();
          this.getCameraInterval();
          this.getProfileList();
          this.welcomeInterval = setInterval(() => {
            this.checkLogin();
            if (this.blackList.length > 0) {
                console.log('BLACK LIST');
                console.log(this.blackList);
                var listCut = [];
                for (var i = 0; i < this.blackList.length; i++) {
                    var timeNow = this.$moment(new Date()).valueOf();
                    if (timeNow - Number((this.blackList[i]).timeBlacklist) > Number(APP_CONFIG.CLEAR_BLACKLIST)) {
                        listCut.push(i);
                    }
                }
                for (var j = listCut.length - 1; j >= 0; j--) {
                    this.blackList.splice(listCut[j], 1);
                }
            }
            if (this.lastEvent) {
                if ((this.$moment(new Date()).valueOf() - this.$moment(this.lastEvent.createdAt).valueOf()) > Number(APP_CONFIG.CLEAR_LASTWELCOME)) {
                    this.eventShow = null;
                    this.messageShow = '';
                    this.lastEvent = {};
                }
            }
            this.getListDisplaying();
            // console.log('CON LAI .........');
            // console.log(this.blackList);
            while (this.queueWelcome.length > 0) {
                this.temp = this.queueWelcome[0];
                this.queueWelcome.shift();
                if (!this.checkExits(this.blackList, this.temp)) {
                    this.setShowWelcome(this.temp.event, this.temp.message);
                    this.temp.timeBlacklist = this.$moment(new Date()).valueOf();
                    this.blackList.push(this.temp);
                    this.getListDisplaying();
                    break;
                } else {
                    for (var y = 0; y < this.blackList.length; y++) {
                        if (this.temp.event.profileId === this.blackList[y].event.profileId) {
                            this.blackList[y].event = this.temp.event;
                            this.blackList[y].timeBlacklist = this.$moment(new Date()).valueOf();
                            this.getListDisplaying();
                            break;
                        }
                    }
                }
            }
        }, (Number(APP_CONFIG.TIME_NEXT_TO_WELCOME)));
          // this.setLastID();
          // :liveUrl="viewSrc.url"
      });
  },
  computed: {

  },
  created() {
    this.checkLogin();
    // console.log(this.userToken);
  },
  methods: {
    Logout() {
      localStorage.removeItem('currentUserToken');
      this.$router.push('/login');
    },
    checkLogin() {
      var token = localStorage.getItem('currentUserToken');
      if (token == null) {
        this.$router.push('/login');
      }
      axios.get(APP_CONFIG.API_URL + '/aiview/getUserInfo', {
        headers: {
          'Authorization': `Bearer ${token}` 
        }
      }).then(() => {
          // console.log('Xác minh thành công');
        }).catch(() => {
          this.$router.push('/login');
        })
    },
    reloadData() {
        if (this.fetchDataInterval) {
            clearInterval(this.fetchDataInterval);
        }
        this.fetchDataInterval = setInterval(() => {
            this.fetchDataList();
            // this.fetchMessageList();
            // this.createQueue();
            this.buildTableWelcome();
        }, APP_CONFIG.ATTENDANCE_FACE_INTERVAL);
        // this.isShowLoading = true;
        // this.isShowLoading = false;
    },
    async buildTableWelcome() {
        // console.log(this.blackList);
        this.tableData = this.blackList.map(item => {
            return {
                welcome: item.message
            };
        });
        return this.tableData;
    },
    async getEventNewestListPerCam(cameraId, idEvent) {
      var eventsTest = [];
      var token = localStorage.getItem('currentUserToken');
      // console.log(APP_CONFIG.API_URL + '/event-newest?cameraId=' + String(cameraId) +'&type=welcome&eventId=' + String(idEvent));
      try {
        await axios.get(APP_CONFIG.API_URL + '/event-newest?cameraId=' + String(cameraId) +'&type=welcome&eventId=' + String(idEvent), {
        headers: {
          'Authorization': `Bearer ${token}` 
        }
      }).then(response => {
        if (response.data.length > 0) {
          eventsTest = response.data; 
        }
        });
      } catch(e) {
        if (e.response) {
          // console.log(e.response.data);
          // console.log(error.response.status);
          // console.log(error.response.headers);
        }
      }
        // console.log(eventsTest);
        return eventsTest;
    },
    updateIdMark() {
        this.getEventNewestAsLandMark();
        this.idMark = this.eventLandMark[0].id;
    },
    genLiveUrl(camera) {
      // console.log(camera);
      var linkView = '';
      if (camera.subUrl && (camera.subUrl.includes('http') || camera.subUrl.includes('https'))) {
        linkView = camera.subUrl;
      }
      if (camera.value && (camera.value.includes('http') || camera.value.includes('https'))) {
        linkView = camera.value;
      }
      if (linkView !== '') {
        // console.log(linkView);
        return linkView;
      }
    },
    getNewWelcomeList(value) {
        this.newWelcomeList = [];
        // console.log(this.profiles);
        for (var p = 0; p < value.length; p++) {
            for (var j = 0; j < this.profiles.length; j++) {
                if (value[p].profileId === this.profiles[j].id && this.profiles[j].data) {
                    this.newWelcomeList.push(this.profiles[j].data.welcomeMessage);
                }
            }
        }
        // console.log('LIST CHAO MUNG MOI');
        // console.log(this.newWelcomeList);
        return this.newWelcomeList;
    },
    async fetchDataList() {
        if (this.lastID === -1) {
            return [];
        }
        console.log('LASTID DE LAY SK');
        console.log(this.lastID);
        // var allEvents = [];
        // console.log(this.events);
        this.events = [];
        // console.log('CAMERA SENSORS DA CHON');
        // console.log(this.sensorsSelected);
        const response = await Promise.all(
            this.sensorsSelected.map(camera => {
                if (camera.id) {
                  const eventPerCam = this.getEventNewestListPerCam(camera.id, this.lastID - 10000);
                  // console.log(eventPerCam);
                  return eventPerCam;
                } else {
                  return [];
                }
            })
        );
        // console.log(response.flat());
        if (this._.flatten(response).length < 1) {
            return [];
        }
        const allEvents = this._.sortBy(this._.flatten(response), 'createdAt', 'asc').reverse();
        this.lastID = allEvents[0].id;
        this.events = this._.uniqBy(allEvents, 'profileId');
        // console.log(this.events);
        // console.log('================LIST EVENT MOI DE LAY MESS=============');
        // console.log(this.events);
        if (this.events.length > 0) {
          this.getNewWelcomeList(this.events);
          this.updateIdMark();
          this.lastID = this.idMark;
          console.log('LASTID update');
          console.log(this.lastID);
        }
        // console.log('TAT CA CHAO MUNG CAN DC HIEN THI');
        // console.log(this.newWelcomeList);
        // console.log(this.lastID);
        // console.log('EVENT TAO QUEUE');
        // console.log(this.newWelcomeList);
        // console.log(this.events);
        if (this.events.length > 0 && this.newWelcomeList.length > 0 && this.newWelcomeList.length === this.events.length) {
          for (var i = 0; i < this.events.length; i++) {
            var temp = {
                event: this.events[i],
                message: this.newWelcomeList[i]
            };
            if (!this.checkExits(this.queueWelcome, temp)) {
                this.queueWelcome.push(temp);
            }
          }
          console.log('QUEUE');
          console.log(this.queueWelcome);
          this.queueWelcome = JSON.parse(JSON.stringify(this.queueWelcome));
        }
        // return this.queueWelcome, this.lastID;
    },
    setShowWelcome(event, message) {
        this.lastEvent = event;
        this.eventShow = event;
        this.messageShow = message;
    },
    checkExits(array, item) {
        var count = 0;
        for (var i = 0; i < array.length; i++) {
            if (Number(item.event.id) === Number((array[i].event).id)) {
              console.log('HAI CAI TRUNG NHAU NHU SAU');
              console.log(item);
              console.log(array[i]);
                count += 1;
            }
        }
        if (count > 0) {
            return true;
        } else {
            return false;
        }
    },
    getListDisplaying() {
        if (!this.blackList) {
            return [];
        } else {
            var listTemp = this.blackList.map(item => {
                return item.event;
            });
            listTemp = this._.sortBy(this._.flatten(listTemp), 'createdAt', 'asc').reverse();
            this.listItemDisplay = listTemp.slice(0, APP_CONFIG.MAX_ATTENDANCE_FACE);
            return this.listItemDisplay;
        }
    },
    getEventNewestAsLandMark() {
      var token = localStorage.getItem('currentUserToken');
      axios.get(APP_CONFIG.API_URL + '/event-newest?count=1&type=welcome', {
        headers: {
          'Authorization': `Bearer ${token}` 
        }
      }).then(response => {
            this.eventLandMark = response.data;
            console.log('EVENT LANMARK:==');
            console.log(this.eventLandMark);
            this.setLastID();
            console.log('ID MARK:');
            console.log(this.eventLandMark[0].id);
            return this.eventLandMark;
        })
    },
    setLastID() {
        // console.log('---------1----------');
        // console.log(this.eventLandMark);
        if (this.eventLandMark[0]) {
            this.lastID = this.eventLandMark[0].id;
            // console.log('LAST ID:', this.lastID);
        }
        // console.log('---------2----------');
        // console.log(this.eventLandMark);
    },
    display(date) {
        if (this.$moment(date).isSame(this.$moment(), 'day')) {
            return this.$moment(date).format('HH:mm:ss');
        }
        if (this.$moment(date).isSame(this.$moment(), 'year')) {
            return this.$moment(date).format('DD/MM - HH:mm:ss');
        }
        return this.$moment(date).format('DD/MM/YYYY');
    },
    async getMonitorList() {
      var token = localStorage.getItem('currentUserToken');
      axios.get(APP_CONFIG.API_URL + '/aiview/getAllMonitor', {
        headers: {
          'Authorization': `Bearer ${token}` 
        }
      }).then(response => {
            this.monitors = response.data.data;
            // console.log('===============MONITORS=========');
            // console.log(this.monitors);
            return this.monitors;
        })
    },
    getCameraInterval() {
      this.fetchCameraInterval = setInterval(() => {
            this.getCameraDisplay();
        }, APP_CONFIG.REFRESH_CAMERA);
    },
    getCameraDisplay() {
      this.camerasDisplay = [];
      // console.log(this.cameras);
      // console.log(this.monitors);
      if (this.cameras && this.monitors) {
        // console.log('VAO DUOC DAY ROI');
        for (var i = 0; i < this.monitors.length; i++) {
          for (var j = 0; j < this.cameras.length; j++) {
            if ((this.monitors[i]).cameraId == (this.cameras[j]).id && (this.monitors[i]).coreId === 4) {
              // console.log('OK');
              this.camerasDisplay.push(this.cameras[j]);
            }
          }
        }
      }
      // console.log('CAMERA FILTERD');
      // console.log(this.camerasDisplay);
      return this.camerasDisplay;
    },
    async getCameraList() {
      var token = localStorage.getItem('currentUserToken');
      axios.get(APP_CONFIG.API_URL + '/aiview/getAllcamera', {
        headers: {
          'Authorization': `Bearer ${token}` 
        }
      }).then(response => {
            this.cameras = response.data.data;
            // console.log('========CAMERAS======');
            // console.log(this.cameras);
            return this.cameras;
        })
    },
    async getProfileList() {
      var token = localStorage.getItem('currentUserToken');
      axios.get(APP_CONFIG.API_URL + '/profile', {
        headers: {
          'Authorization': `Bearer ${token}` 
        }
      }).then(response => {
            this.profiles = response.data;
            console.log(this.profiles);
            return this.profiles;
        })
    },
    genListOptions(cameras) {
      // console.log(cameras);
      if (cameras) {
        return cameras.map(item => {
          return {
            id: item.cameraId,
            label: item.cameraName,
            value: item.liveUrl,
            subUrl: item.analyticsUrl
          }
        })
      }
    },
    filterText(value) {
        if (value) {
            var temp = value.split('-');
            return temp;
        }
    },
    updateCameraSensor() {
      this.reloadData();
    }
  },
  beforeDestroy() {
      this.showHide = true;
      if (this.fetchDataInterval) {
          clearInterval(this.fetchDataInterval);
      }
      if (this.welcomeInterval) {
          clearInterval(this.welcomeInterval);
      }
      if (this.fetchCameraInterval) {
          clearInterval(this.fetchCameraInterval);
      }
  },
  watch: {
    sensorsSelected() {
      console.log(this.sensorsSelected);
      this.updateCameraSensor();
    },
    viewSrc() {
      console.log(this.viewSrc);
    }
  },
  data () {
    return {
      idMark: 0,
      eventLandMark: {},
      blackList: [],
      userToken: null,
      lastEvent: {},
      eventShow: null,
      camerasDisplay: [],
      newWelcomeList: [],
      monitors: [],
      fetchDataInterval: null,
      temp: {},
      queueWelcome: [],
      // isShowLoading: true,
      welcomeInterval: null,
      fetchCameraInterval: null,
      messageShow: '',
      right: false,
      cameras: [],
      listItemDisplay: [],
      BASE_URL: APP_CONFIG.API_URL,
      profiles: [],
      events: [],
      selectedCameraView: [],
      selectedCameraSensors: [],
      viewSrc: '',
      sensorsSelected: [],
      columns: [
        {
          name: 'desc',
          required: true,
          label: 'Phòng ban',
          align: 'left',
          field: row => row.name,
          format: val => `${val}`,
          sortable: true,
          classes: 'ellipsis',
          style: 'max-width: 100px',
          headerClasses: 'bg-primary text-white'
        },
        { name: 'calories', align: 'center', label: 'Sĩ số', field: 'calories', sortable: true, headerClasses: 'bg-secondary text-white', classes: 'ellipsis',
          style: 'max-width: 100px', }
      ],
      data: [
        {
          name: 'Phòng 1',
          calories: '1/2'
        },
        {
          name: 'Phòng 2',
          calories: '2/3'
        },
        {
          name: 'Phòng 3',
          calories: '1/3'
        },
        {
          name: 'Phòng 4',
          calories: '1/1'
        },
        {
          name: 'Phòng 5',
          calories: '1/3'
        },
        {
          name: 'Phòng 6',
          calories: '1/2'
        },
        {
          name: 'Phòng 7',
          calories: '2/3'
        },
        {
          name: 'Phòng 8',
          calories: '1/3'
        },
        {
          name: 'Phòng 9',
          calories: '1/1'
        },
        {
          name: 'Phòng 10',
          calories: '1/3'
        },
        {
          name: 'Phòng 11',
          calories: '1/2'
        },
        {
          name: 'Phòng 12',
          calories: '2/3'
        },
        {
          name: 'Phòng 13',
          calories: '1/3'
        },
        {
          name: 'Phòng 14',
          calories: '1/1'
        },
        {
          name: 'Phòng 15',
          calories: '1/3'
        }
      ]
    }
  }
}
</script>
